import {
  Typography
} from "@material-ui/core";
import { Button } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Header from "./Header";
import AddIcon from '@material-ui/icons/Add';
import AddProduct from "./AddProduct";
import EditProduct from "./EditProduct";
import ProductList from "./ProductList";
import DeleteConfirm from "./DeleteConfirm";
const useStyles = makeStyles(theme => ({

}));
const columns = [
  { id: "id", label: "Product ID", minWidth: 170 },
  { id: "ProductName", label: "Product Name", minWidth: 100 },
];
export default function DashBoardTable() {
  const [open, setOpen] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);
  const [openDelete, setOpenDelete] = useState(false);
  let toAdd = "";
  const [toEdit, setEdited] = useState(null);
  const setEdit = (value) => {
    console.log("Setting " + value);
    setEdited(value);


  }
  const [toDelete, setDelete] = useState(null);
  const [toEditName, setEditName] = useState(null);
  const initial = [];
  const [rows, setRows] = useState(initial);
  const [, forceUpdate] = useState();

  useEffect(() => {
    async function fetchData() {

      const response = await fetch("/product", {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      }).then((response) => response.json()).then((data) => {
        return data.result


      });

      return response
    }
    let a = fetchData()
    a.then((data) => {
      setRows(data)

    })

    console.log("QWEW " + rows)

  }, []);

  // function createData(id, ProductName) {
  //   return { id, ProductName };
  // }

  const handleDeleteClose = () => {
    setOpenDelete(false);
    console.log(openDelete)
  };
  const deleteProduct = async (e) => {
    let id = e;
    // setRows(rows.filter((e) => (e.id !== id)))

    let result = "";
    await fetch("/product/" + e, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      }
    }).then((response) => response.json()).then((data) => {
      result = data
    })
    console.log(result)

    setOpenDelete(false);
    window.location.reload();
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleConfirm = async (value) => {
    if (value.name != "") {
      const data = { ProductName: value }
      let result = "";
      await fetch("/product", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data)
      }).then((response) => response.json()).then((data) => {
        result = data
      })
      console.log(result)
      handleClose();
      window.location.reload();
    } else {
      alert("Please Enter Some Value")

    }
  };
  const handleEditClose = () => {
    setOpenEdit(false);

  };
  const handleEditConfirm = async (Product) => {
    if (toEdit != "") {
      // const newRows = rows.map((obj) => {
      //   if (obj.id == toEdit) {

      //     obj.name = Product;
      //   }
      //   return obj
      // });
      // setRows(newRows)

      const data = { ProductName: Product }
      let result = "";
      await fetch("/product/" + toEdit, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data)
      }).then((response) => response.json()).then((data) => {
        result = data
      })
      console.log(result)
      setOpenEdit(false);
      window.location.reload();
    }
    else {
      alert("Please Enter Some Value");
    }
  };
  return (
    <div>
      {open === true &&
        <AddProduct
          open={open}
          rows={rows}
          handleClose={handleClose}
          handleConfirm={handleConfirm}
        // createData={createData}

        />}
      {openEdit === true &&
        <EditProduct
          openEdit={openEdit}
          rows={rows}
          handleEditClose={handleEditClose}
          handleEditConfirm={handleEditConfirm}
          toEditName={toEditName}


        />
      }
      {openDelete === true &&
        <DeleteConfirm
          openDelete={openDelete}
          rows={rows}
          handleDeleteClose={handleDeleteClose}
          deleteProduct={deleteProduct}
          toDelete={toDelete}
        />}
      <Header />
      <Button
        variant="contained"
        style={{ backgroundColor: "#3F51B5", color: "white", left: "89.5%", marginTop: 45, marginBottom: "-2rem" }}
        startIcon={<AddIcon />}
        onClick={() => {
          setOpen(true);
        }}
      >
        Add Product
    </Button>
      <Typography style={{ marginLeft: "3rem", fontWeight: "bold", color: "#3F51B5" }}>Product Management</Typography>
      <ProductList
        rows={rows}
        columns={columns}
        setEdit={setEdit}
        setDelete={setDelete}
        setEditName={setEditName}
        setOpenEdit={setOpenEdit}
        setOpenDelete={setOpenDelete}
        deleteProduct={deleteProduct}
        handleDeleteClose={handleDeleteClose}
      />


    </div >
  );
}
