
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Typography
} from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Button } from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import TablePagination from '@material-ui/core/TablePagination';
import React, { useState, useEffect } from "react";
const useStyles = makeStyles(theme => ({
    '@global': {
        '*::-webkit-scrollbar': {
            width: '1em',
            border: '1em',
            borderColor: "white"
        },
        '*::-webkit-scrollbar-track': {
            '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)'
        },
        '*::-webkit-scrollbar-thumb': {
            backgroundColor: 'rgb(55,100,160)',
            outline: '1px solid red'
        }
    },
    root: {
        width: "100%",
    },
    container: {
        maxHeight: 750,
        marginTop: 50,
        marginLeft: 50,
        marginRight: 400,
        width: "95%",
    }
}));
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.background.default,
            borderLeft: "1rem"
        },

        '&:nth-child(2)': {
            textAlign: "center"

        },

        '&:nth-child(1)': {
            textAlign: "center"

        },

        '&:nth-child(3)': {
            textAlign: "center"

        }
    },
}))(TableRow);
const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: "#3F51B5",
        color: theme.palette.common.white,
        '&:nth-child(2)': {
            textAlign: "center"
        }
        ,
        '&:nth-child(1)': {
            textAlign: "center"
        }
        ,
        '&:nth-child(3)': {
            textAlign: "right",
            paddingRight: "6.2%"
        }
    },
    body: {
        fontSize: 14,
        '&:nth-child(2)': {
            textAlign: "center"

        },
        '&:nth-child(1)': {
            textAlign: "center"

        },
        '&:nth-child(3)': {
            textAlign: "right"
        }
    },
    root: {},
}))(TableCell);

export default function ProductList(props) {
    const classes = useStyles();
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };
    useEffect(() => { console.log("Rendering Product List") }, [])

    return (
        <div>
            <TableContainer component={Paper} className={classes.container}>
                <Table className={classes.root} stickyHeader aria-label="sticky table">
                    <TableHead>
                        <StyledTableRow>
                            {props.columns.map(column => (
                                <StyledTableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{
                                        minWidth: column.minWidth
                                    }}
                                >
                                    {column.label}
                                </StyledTableCell>

                            )
                            )
                            }
                            <StyledTableCell
                                key="option"
                                align="right"
                                style={{
                                    minWidth: 170
                                }}
                            >
                                Option
                                </StyledTableCell>
                        </StyledTableRow>
                    </TableHead>
                    <TableBody>


                        {console.log(Object.keys(props.rows))}
                        {props.rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).
                            map(row => {

                                return (
                                    <StyledTableRow hover role="checkbox" tabIndex={-1} key={row.id}>

                                        <StyledTableCell align="left">
                                            {row.id}
                                        </StyledTableCell>
                                        <StyledTableCell align="left" >
                                            {row.ProductName}
                                        </StyledTableCell>

                                        <StyledTableCell align="left" >
                                            <Button
                                                variant="outlined"
                                                color="primary"
                                                style={{ marginRight: "0.5rem" }}
                                                startIcon={<EditIcon />}
                                                onClick={() => {
                                                    props.setEdit(row.id);
                                                    props.setEditName(row.ProductName);
                                                    console.log(row.ProductName);
                                                    // alert(row.id + " " + row.name);
                                                    props.setOpenEdit(true);
                                                }}>
                                                Edit
                                        </Button>
                                            <Button
                                                variant="outlined"
                                                color="secondary"
                                                startIcon={<DeleteIcon />}
                                                onClick={
                                                    () => {
                                                        // props.deleteProduct(row.id);
                                                        props.setDelete(row.id);
                                                        props.setOpenDelete(true);

                                                    }
                                                }
                                            >Delete
                                        </Button>


                                        </StyledTableCell>
                                    </StyledTableRow>
                                );
                            })}
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[10, 25, 60, 100]}
                component="div"
                count={props.rows.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
                style={{ marginRight: "1.8rem", color: "#3F51B5" }}
            />
        </div>






    );






}