import React, { useState, useEffect } from "react";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
function EditProduct(props) {
    let name = props.toEditName;
    console.log("toEditName = " + name);
    const [Product, setProduct] = useState(name)
    useEffect(() => { setProduct(name); console.log("PRODUCT + " + Product) }, [])
    const handleInputChange = event => {
        setProduct(event.target.value);
    }
    const confirm = () => {
        props.handleEditConfirm(Product);
    }

    return (
        <div>
            <Dialog open={props.openEdit} onClose={props.handleEditClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Edit Product</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Enter Product Name:
        </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="product_name"
                        label="Product Name"
                        type="text"
                        fullWidth
                        value={Product}
                        onChange={handleInputChange}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={props.handleEditClose} color="primary">
                        Cancel
        </Button>
                    <Button onClick={confirm} color="primary">
                        Confirm Change
        </Button>
                </DialogActions>
            </Dialog>
        </div >
    );
}

export default EditProduct;
