import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import {
  Link
} from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import DashboardIcon from '@material-ui/icons/Dashboard';
const useStyles = makeStyles(theme => ({
  container: {
    flex: 1,
    width: "100%",

    height: "100%",
    position: 0
  },
  avatar: {
    margin: theme.spacing(1),
    // backgroundColor: theme.palette.secondary.main,
    backgroundColor: "#3F51B5",
  },
  root: {
    flexGrow: 1
  },
  menuButton: {
    marginRight: theme.spacing(2)
  },
  title: {
    flexGrow: 1
  },
  account: { marginLeft: "65%" }
}));
const Header = () => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <div className={classes.container}>
      <AppBar position="static" style={{ backgroundColor: "white" }}>
        <Toolbar>
          <div style={{ display: "inline-flex", width: "30%" }}>
            <Avatar className={classes.avatar}>
              <DashboardIcon />
            </Avatar>
            <Typography style={{ fontSize: 17.5, color: "#3F51B5", width: "80", marginTop: "auto", marginBottom: "auto" }}>
              Admin Dashboard
          </Typography>
          </div>
          <div className={classes.account}>

            <Link to="/"> <Typography style={{ color: "#3F51B5" }}>Logout</Typography></Link>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
};
export default Header;
